.dashboard-login-aligner {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-pack: center;
	    justify-content: center;
	position: fixed;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	background: #1ac;
}

.dashboard-login-aligner-item {
  color: #fff;
  text-align: center;
}

.dashboard-login-input {
  text-align: center;
  display: block;
  width: 100%;
  background: #fff;
  color: #333;
  border-radius: 5px;
  border: none;
  padding: 12px 15px;
  font-size: 16px;
  margin: 20px auto;
}

.dashboard-login-title {
  color: white;
  font-weight: 100;
}

.dashboard-login-btn {
  display: block;
  text-align: center;
  font-family: 'Lato', sans-serif;
  font-weight: 400;
  font-size: 16px;
  width: 100%;
  padding: 10px 15px 12px;
  border-radius: 5px;
  border: 1px solid #fff;
  color: #fff;
  opacity: 0.8;
  -webkit-appearance: none;
}

.dashboard-logout-btn:hover {
  cursor: pointer;
  opacity: 1;
}

.dashboard-logout-btn {
  font-family: 'Lato', sans-serif;
  font-weight: 400;
  font-size: 16px;
  padding: 10px 15px 12px;
  border-radius: 5px;
  background-color: #1ac;
  color: white;
  border: 1px solid #1ac;
  opacity: 0.8;
  -webkit-appearance: none; 
}

.dashboard-error {
  color: #ee3344;
}

.dashboard-courses-container {
  padding: 15px;
}



